
div.core_nothing{
            text-align: center;
            font-size: 1.8rem;
            padding-top:4rem;
            color: #999;
}
div.add_button{
            margin-top:3rem;
            margin-bottom:3rem;
            text-align:center;
}
div.edit-button{
            position: absolute;
            right: 1rem;
            top: 0.75rem;
}
div.edit-button i{
            font-size: 1.6rem;
            color: gray;
}
div.edit-button a{
            margin-left:1rem;
            cursor: pointer;
}
div.core_list{
            border-bottom: 1px solid #dedede;
            min-height: 10.8rem;
            padding:1rem 0.5rem;
            position: relative;
}
div.core_list:hover{
            background-color: #f6f6f6;
}
div.core_list div.server_core_avatar{
            width: 6rem;
            height: 6rem;
            border: 1px solid #999;
            border-radius: 0.75rem;
            position: relative;
            float: left;
}
div.server_core_avatar div.no-pic{
            position:absolute;
            width: 5.8rem;
            height: 5.8rem;
            font-size:1.3rem;
            text-align: center;
            padding-top:0.6rem;
            color: gray;
}
div.server_core_avatar img.proj_avatar{
            display: block;
            position: absolute;
            left: 0.12rem;
            top: 0.12rem;
            width:5.6rem;
            height: 5.6rem;
}
div.server_core_info{
            padding-left: 8rem;
            width: 100%;
}
div.server_core_info div.list_title{
            font-size:2rem;
            font-weight: bold;
            line-height: 1em;
            margin-bottom:0.7rem;
}
div.list_row{
            line-height: 1.4em;
            width: 100%;
}
div.list_row div.half{
            display: inline-block;
            width: 45%;
}
div.half span.ttl{
            margin-right: 0.5rem;
            display: inline-block;
}
div.note{
            width: 100%;
            color: #999;
}
span.default-text{
            color: #999;
}
input.form-control,
        select.form-control{
            width: 75%;
}
div.form_input textarea{
            width: 75%;
            height: 8rem;
}
div.edit_model_content div.form_group{
            line-height: 2em;
            font-size: 1.5rem;
            margin-top: 1.2rem;
            margin-bottom: 1.2rem;
}
div.edit_model_content div.form_label{
            position: relative;
            float:left;
            min-width: 8rem;
            width: 25%;
}
div.edit_model_content div.form_input{
            width: 100%;
            padding-left: 25%;
}
div.error-hint{
            font-size: 1.25rem;
            text-align: right;
            color: red;
}



