
div.pg-hint{
    width: 100%;
}
div.pg-hint div.pg-label{
    position: relative;
    margin-top: 0.8rem;
    height: 1.3rem;
    margin-bottom: 0.8rem;
}
div.pg-label span.upload-label{
    display: inline-block;
    float: left;
    position:relative;
    font-size: 1.3rem;
}
div.pg-label span.percent{
    display: inline-block;
    float: right;
    position:relative;
    font-size: 1.3rem;
}
.colored{
    color: darkblue;
}
.colored-error{
    color: red;
}



